import React, { useState } from 'react';
import { BarChart, Bar, XAxis, YAxis, Tooltip, Legend, CartesianGrid } from 'recharts';

function FinanceManager() {
  const [income, setIncome] = useState(0);
  const [expense, setExpense] = useState(0);
  const [salary, setSalary] = useState(0);
  const [bonus, setBonus] = useState(0);
  const [netIncome, setNetIncome] = useState(0);

  const handleCalculate = () => {
    const incomeValue = parseFloat(income) || 0;
    const expenseValue = parseFloat(expense) || 0;
    const salaryValue = parseFloat(salary) || 0;

    const calculatedBonus = salaryValue * 0.1; //  10%
    const calculatedNetIncome = incomeValue - expenseValue + calculatedBonus;

    setBonus(calculatedBonus);
    setNetIncome(calculatedNetIncome);
  };

  const data = [
    { name: '', value: parseFloat(income) || 0 },
    { name: '', value: parseFloat(expense) || 0 },
    { name: '', value: netIncome }
  ];

  return (
    <div>
      <h2></h2>
      
      <div>
        <label>
          :
          <input type="number" placeholder="" onChange={(e) => setIncome(e.target.value)} />
        </label>
      </div>
      
      <div>
        <label>
          :
          <input type="number" placeholder="" onChange={(e) => setExpense(e.target.value)} />
        </label>
      </div>
      
      <div>
        <label>
          :
          <input type="number" placeholder="" onChange={(e) => setSalary(e.target.value)} />
        </label>
      </div>
      
      <button onClick={handleCalculate}></button>

      <div>
        <h3></h3>
        <p>: {bonus.toFixed(2)}</p>
        <p>: {netIncome.toFixed(2)}</p>
      </div>

      <BarChart width={500} height={300} data={data}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="name" />
        <YAxis />
        <Tooltip />
        <Legend />
        <Bar dataKey="value" fill="#8884d8" />
      </BarChart>
    </div>
  );
}

export default FinanceManager;
